import {css} from 'styled-components';


export const mobile = (props) =>{
    return css`
    @media only screen and (max-width:480px){
        ${props}
    }
`;
};

export const tablet = (props) =>{
    return css`
    @media only screen and (max-width:840px){
        ${props} // TABLET 840/850/800 TO 1280
    }
`;

};